:root {
    --color-scheme: dark;
    --background-color: #1D2630;
    --text-color: #bdc1c6;
    --header-color: #4169E1;  // website title
    --header-link-color: #4169E1;
    --header-link-hover: #8463B5;
    --header-link-active: #9F7EDB;
    --title-color:  #D1D4D2;  // post title
    --post-list-hover: #007BFF;
    --headline-color: #D1D4D2;
    --link-color: #00BFFF;
    --link-hover-color: #4DA8DA;
    --code-background: #F5F5F5;
    --code-color: #1A1A1A;
    --figcaption-color: #999;
    --blockquote-color: #999;
    --blockquote-border: #333;
    --table-border: #444;
    --table-alt-background: #2a2a2a;
    --footer-background: #222;
    --date-color: #777;
    --horizontal-link-border: #e88;
    --tag-link-color: #666;
    --tag-link-hover: #999;
    --paginator-disabled: #666;
    --current-page-color: #666;
    --location-color: #777;
    --location-border: #555;
}

@import 'monokai-dark';
